body {
  background-color: #0ff;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 20px;
  max-width: 600px;
}

.newnote {
  background-color: #fff;
  padding: 10px 12px;
  border-radius: 5px;
  box-shadow: 2px 2px 2px black;
  input {
    width: 100%;
    border: none;
    font-size: 20px;
    &:focus {
      outline: none;
    }
  }
}

.noteList {
  margin-top: 2em;
}
.note {
  margin-top: 1em;
  background-color: #fff;
  padding: 10px 12px;
  border-radius: 2px;
  box-shadow: 1px 1px 1px black;
  font-size: 18px;
}
